html, body {
    width: 100% !important;
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    background-color: #f6f6f6 !important;
    font-family: Open, sans-serif;
}


.App {
    width: 100vw;
    flex-wrap: wrap;
}

.title {
    text-align: left;
    color: white;
}

.form {
    display: flex;
    flex-direction: column;
    flex: 1;
    padding: 10px;
    align-items: center;
    margin: 10px;
}

.results {
    display: flex;
    flex-direction: column;
    flex: 1;
    padding: 10px;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-top: 30px;
}

.infoBox {
    display: flex;
    flex-direction: column;
    flex: 1;
    padding: 10px;
    align-items: center;
    justify-content: center;
    width: 40%;
    margin-top: 15px;
}

@media only screen and (max-width: 1550px) {
    .infoBox {
        width: 60%;
    }
}

@media only screen and (max-width: 600px) {
    .infoBox {
        width: 90%;
    }
}

@media only screen and (max-width: 350px) {
    .infoBox {
        width: 95%;
    }
}


.resultsSubtitle {
    font-size: 20px;
    font-weight: 600;
    text-align: left;
    width: 100%;
}

.costInfo {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    border: 0 solid #c9c9c9;
    border-bottom-width: 1px;
    padding-inline-start: 5px;
    padding-inline-end: 5px;
    margin-top: 10px;
}

.rentInfo {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.amountBox {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.totalResults {
    margin-top: 20px;
}

.costOfLiving {
    margin-top: 20px;
}

.totalCost {
    border-color: red;
}

.totalType {
    font-weight: 600;
}

.totalCost {
    font-weight: 600;
}

.resultsTitle {
    font-size: 25px;
    font-weight: 500;
    text-align: left;
}

.inputBox {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.row {
    flex-direction: column;
    width: 100%;
    align-items: flex-start;
    text-align: center;
    padding: 5px;
    margin-right: 30px !important;
}

.inputGroup {
    width: 100% !important;
    margin-bottom: 10px;
    margin-top: 10px;
}

.rowPara {
    vertical-align: middle;
    font-size: 25px;
    margin: 0;
}

.submitButton {
    margin-top: 10px;
    background-color: #5168d7 !important;
    width: 250px;
}


.titleDiv {
    width: 100%;
    background-color: #5168d7;
    padding: 10px;
}

.inputCustomText {
    border-width: 0 !important;
    border-bottom-width: 1px !important;
    background-color: transparent !important;
    border-radius: 0 !important;
    font-size: 20px !important;
    padding-block-start: 0 !important;
    padding-inline-start: 0 !important;
}

.inputCustomText:focus {
    outline: 0 !important;
    -webkit-appearance: none;
    box-shadow: none !important;
}

.costOfLivingInput {
    border-radius: 0 !important;
    font-size: 16px !important;
    padding-block-start: 0 !important;
    padding-block-end: 0 !important;
    padding-inline-start: 5 !important;
    vertical-align: middle;
    width: 25% !important;
    margin-right: 10px;
}

.rentInput {
    border-radius: 0 !important;
    font-size: 16px !important;
    padding-block-start: 0 !important;
    padding-block-end: 0 !important;
    padding-inline-start: 5 !important;
    vertical-align: middle;
    width: 20% !important;
}

.costOfLivingType {
    border-radius: 0 !important;
    font-size: 16px !important;
    padding-block-start: 0 !important;
    padding-block-end: 0 !important;
    padding-inline-start: 5 !important;
    vertical-align: middle;
    width: 50% !important;
}

.modalStyle {
    display: flex !important;
    text-align: center;
    flex: 1;
    align-items: center !important;
    justify-content: center !important;
}

.modalText {
    display: flex !important;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
    margin-right: 20px;
    height: 100%;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
}

.addCostButton {
    margin-top: 15px;
    background-color: #5168d7 !important;
}
